import React from 'react'
import AuthContext from '../context/auth-context'


class SellPage extends React.Component {

    constructor(props){
        super(props)

        console.log(this.props)
        this.state = {
            title:"",
            description:"",
            isbn:"",
            author:"",
            price:0,
            image:"",
            imageValue:""
        }
    }


    static contextType = AuthContext


    handleSellBook = () => {

        const formData = new FormData()
        formData.append('title',this.state.title)
        formData.append('description',this.state.description)
        formData.append('isbn',this.state.isbn)
        formData.append('author',this.state.author)
        formData.append('price',this.state.price)
        formData.append('image',this.state.image)
        formData.append('sellerid',this.context.userId)
        
        if(this.props.branchid)
            formData.append('branchid',this.props.branchid)
        // else
        //     formData.append('sellerid',this.context.userId)
            
        //Send the formData to server
        fetch(this.props.url,{
            method:"POST",
            headers:{
                'Authorization':'bearer '+this.context.token
            },
            body:formData
        }).then((response) => response.json())
        .then(resData => {
            console.log(resData)
            this.setState({
                title:"",
                description:"",
                isbn:"",
                author:"",
                price:0,
                image:null,
                imageValue:""
            })
            
            if(this.props.reflect)
                this.props.reflect()
        }).catch(err => {
            console.log(err)
        })
    }


    handleInputChange = (event) => {



        if(event.target.files){
            this.setState({
                image: event.target.files[0],
                imageValue:event.target.value
            })
            return
        }

        this.setState({
            [event.target.name] : event.target.value
        })

    }


    render(){
        return(
            <div className="container w-50" style={{marginTop:"3rem"}}>
                <form>
                    <div className="form-group">
                        <label style={{margin:0}}>Title</label>
                        <input type="text"
                               className="form-control" 
                               placeholder="Title" 
                               onChange={this.handleInputChange}
                               value={this.state.title}
                               name="title"
                               />
                    </div>

                    <div className="form-group">
                        <label style={{margin:0}}>Description</label>
                        <input type="text" 
                               className="form-control" 
                               placeholder="Description"
                               onChange={this.handleInputChange}
                               value={this.state.description}
                               name="description"
                               />
                    </div>

                    <div className="form-group">
                        <label style={{margin:0}}>ISBN number</label>
                        <input type="text" 
                                className="form-control" 
                                placeholder="ISBN number"
                                onChange={this.handleInputChange}
                               value={this.state.isbn}
                               name="isbn"
                                />
                    </div>

                    <div className="form-group">
                        <label style={{margin:0}}>Author</label>
                        <input type="text"
                               className="form-control" 
                               placeholder="Author"
                               onChange={this.handleInputChange}
                               value={this.state.author}
                               name="author"
                               />
                    </div>

                     <div className="form-group">
                        <label style={{margin:0}}>Price</label>
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <div className="input-group-text">Rs.</div>
                            </div>
                            <input type="text" 
                                    className="form-control" 
                                    placeholder="Price"
                                    onChange={this.handleInputChange}
                                    value={this.state.price}
                                    name="price"
                                    />
                        </div>
                    </div>
                    <div className="form-group">
                        <label style={{margin:0}}>Image</label>
                        <input type="file" className="form-control-file" 
                            name="image" onChange={this.handleInputChange} value={this.state.imageValue}/>
                    </div>
                    <button type="button" onClick={this.handleSellBook} className="btn btn-primary btn-lg float-right">{this.props.btnText}</button>
                </form>
            </div>
        )
    }
}


export default SellPage