import React from 'react'
import AuthContext from '../../context/auth-context'

class CreateBranch extends React.Component{

    constructor(props){
        super(props)

        this.state = {
            branchname:"",
            branchdetails:"",
            classes:"",
            colour:""
        }
    }

    static contextType = AuthContext

    handleInput = (event) => {
        this.setState({
            [event.target.name]:event.target.value
        })
    }


    handleCreateBranch = (event) => {
        console.log(this.state)

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/branch',{
            method:"POST",
            headers:{
                'Content-Type':'application/json',
                'Authorization':'bearer '+this.context.token
            },
            body: JSON.stringify({
               ...this.state
            })
        })
        .then(response => response.json())
        .then(resData => {
            console.log(resData)

            this.props.reflect()
            
            this.setState({
                branchname:"",
                branchdetails:"",
                classes:"",
                colour:"" 
            })
            
        }).catch(err => {
            console.log(err)
        }) 
    }

    render(){

        return (
            <form style={{marginTop:"3rem"}} className="w-50 container">
                <div className="form-group">
                    <label htmlFor="branchName">Branch Name</label>
                    <input type="text" 
                           className="form-control" 
                           placeholder="Branch Name"
                           name="branchname"
                           onChange={this.handleInput}
                           value={this.state.branchname}/>
                </div>
                <div className="form-group">
                    <label htmlFor="branchDetails">Branch Description</label>
                    <input type="text" 
                           className="form-control" 
                           placeholder="Branch Description"
                           name="branchdetails"
                           onChange={this.handleInput}
                           value={this.state.branchdetails}/>
                </div>
                <div className="form-group">
                    <label htmlFor="branchColour">Colour</label>
                    <input type="text" 
                           className="form-control" 
                           placeholder="Colour"
                           name="colour"
                           onChange={this.handleInput}
                           value={this.state.colour}
                           />
                </div>
                <div className="form-group">
                    <label htmlFor="branchIcon">Icon</label>
                    <input type="text" 
                        className="form-control" 
                        placeholder="Icon"
                        name="classes"
                        onChange={this.handleInput}
                        value={this.state.classes}/>
                </div>
                <button type="button" className="btn btn-lg btn-primary float-right" onClick={this.handleCreateBranch}>Create Branch</button>
            </form>
        )
    }
}


export default CreateBranch


