import React, { useState, useEffect } from 'react'

import './SelectedBook.css'

export default function SelectedBook(props) {
  const [bookData, setBookData] = useState(null)
  const [showChat, setShowChat] = useState(false)
  //const [isAddedToCart , setIsAddedToCart] = useState(!bookData ? false : bookData.inCart)

  // const buttonText =
  //   (bookData && (bookData.inCart ? 'Cancel' : 'Add to Cart')) || ' '

  useEffect(() => {
    console.log('Called', props)
    // Api
    if (bookData) return

    const authHeader = 'bearer ' + props.context.token
    fetch(
      process.env.REACT_APP_BASE_URL +
        'bookstore/buybook/bookdetails/' +
        props.book.bookid,
      {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: authHeader,
        },
      }
    )
      .then((response) => response.json())
      .then((resData) => {
        console.log('--BuyBook : SelectedBook ', resData)
        setBookData(resData)
      })
  })

  const bookimg = process.env.REACT_APP_BASE_URL + 'images/' + props.book.image
  let buttonText =
    (bookData &&
      (props.context.userId === bookData.sellerid
        ? 'Messages'
        : 'Contact Seller')) ||
    ''
  if(showChat)
      buttonText = 'Close Chat'

  return !bookData ? null : (
    <React.Fragment>
      <div className='requests-selected' style={{ marginTop: '20px' }}>
        <div className='card' style={{ width: '18rem' }}>
          <img className='card-img-top' src={bookimg} alt='Cardcap' />
          <div className='card-body'>
            <h5 className='card-title'>{bookData.title}</h5>
          </div>
        </div>
        <div className='req-book-details' style={{ marginLeft: '20px' }}>
          <span>ISBN : {bookData.isbn} </span>
          <span>Author:{bookData.author}</span>
          <span>Price: {bookData.price}</span>
          <br></br>
          <span>Description</span>
          <p className='text-justify'>{bookData.description}</p>
        </div>
        <div className='control-btns'>
          <button onClick={props.onBack}>Back</button>
          <button
            onClick={() => {
              if (!showChat) {
                setShowChat(true)
                return props.onContact(bookData.sellerid)
              }
              setShowChat(false)
              props.onContact(null)
            }}
          >
            {buttonText}
          </button>

          {/* <button onClick={() => {
                                         const inCart = !bookData.inCart;
                                         setBookData({
                                             ...bookData,
                                             inCart: inCart
                                         });
                                         props.onCart(inCart,props.book.bookid) }}>{buttonText}</button>    */}
        </div>
      </div>
    </React.Fragment>
  )
}

