import React from 'react'
import '../components/MyCart/MyCart.css'
import CartItem from '../components/MyCart/CartItem'
import Checkout from '../components/MyCart/Checkout';
import AuthContext from '../context/auth-context'

function Cart(props){

    let cost = 0;
    const cartItems = props.cartItems.map((item,i) => {
        cost = cost+ item.price
        return <CartItem key={item.cartid} deleteItem={props.deleteItem} data={item}/>
    })

    return (
            <div className="cart-page">
                <div className="cart-items">
                    {cartItems}
                </div>
                <div className="order">
                    <h1>Order</h1>
                    <span>Total Price</span>
                    <span>Rs. {cost} </span>
                    <button onClick={props.onCheckout}>Proceed to Checkout</button>
                </div>
            </div>
    )
}




class MyCartPage extends React.Component{

    constructor(props){
        super(props)

        this.state = {
            hasItems: false,
            isCheckout:false,
            cartItems: [],
        }
    }

    static contextType = AuthContext

    componentDidMount(){

        //FETCH Cart Item
        const authHeader = "bearer "+this.context.token
        fetch(process.env.REACT_APP_BASE_URL+'bookstore/cart/',{
            method:"GET",
            headers:{
                'Content-Type':'application/json',
                'Authorization': authHeader
            }
        })
        .then( response => response.json())
        .then( resData => {
            console.log('--Cart : ',resData)
            let hasItems = resData.length > 0 ? true : false
            this.setState({
                hasItems:hasItems,
                cartItems:resData
            })
        }) 

    }


    handleCheckout = () => {
        this.setState({
            isCheckout:true
        })
    }


    handleDeleteItem = (bookid) => {

        // Delete the particular number
        const authHeader = "bearer "+this.context.token
        fetch(process.env.REACT_APP_BASE_URL+'bookstore/cart/',{
            method:"DELETE",
            headers:{
                'Content-Type':'application/json',
                'Authorization': authHeader
            },
            body : JSON.stringify({userid:this.context.userId, bookid:bookid})
        })
        .then( response => response.json())
        .then( resData => {
            console.log('--Cart : ',resData)
            const updatedCart = this.state.cartItems.filter(item => {
                return item.bookid !== bookid
            })
            this.setState({
                cartItems:updatedCart,
                hasItems: updatedCart.length > 0 ? true:false
            })
        })
    }



    render(){
        return (

            this.state.isCheckout ? <Checkout/> : 
            this.state.hasItems ? 
                <Cart cartItems={this.state.cartItems} 
                      onCheckout={this.handleCheckout}
                      deleteItem={this.handleDeleteItem}
                      /> : <h1> Sorry </h1>          
        )
    }    
}


export default MyCartPage