import React, { useState , useEffect} from 'react'
import AuthContext from '../context/auth-context'
import OptionSelector from '../components/AdminProducts/OptionSelector'


function ViewRequests(props){

   
    const requests = props.requests.map((request,i)=>{
        return (
            <div key={request.requestid} className="request-box">
                    <h2>{request.title}</h2>
                    <p>{request.seller}</p>
                    <button onClick={props.onView.bind(this,request.requestid, request.seller)}>View</button>
            </div>
        )
    } )

    return(
        <div className="container w-50">
              {requests}
            </div>
        )
}



function SelectedRequest(props){

        console.log(props)

      const branchOptions = props.options.map((branch)=>{
            return <option key={branch.branchid} value={branch.branchid}>{branch.branchname}</option>
        })

     const [request, setRequest] = useState({})

     useEffect(() => {

        if(request.requestid)
            return

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/requests/'+props.requestId,{
            method:"GET",
            headers:{
                'Content-Type':'application/json',
                'Authorization':'bearer '+props.context.token
            }
        })
        .then(response => response.json())
        .then(resData => {
            console.log(resData)
            setRequest(resData)
        })
    })

    const requestimg = process.env.REACT_APP_BASE_URL+'images/'+request.image

    return(
        <div className="container" style={{marginTop:"20px"}}>
            <OptionSelector defaultOption="Select a branch"
                            options={branchOptions}
                            value={props.value}
                            onChange={props.onChange}    
                            />
            <div className="requests-selected">
                <div className="card" style={{width:"18rem"}}>
                    <img className="card-img-top" src={requestimg} alt="Cardcap"/>
                    <div className="card-body">
                        <h5 className="card-title">{request.title}</h5>
                        </div>
                </div>
                 <div className="req-book-details">
                        <span>ISBN : {request.isbn} </span>
                        <span>Author:{request.author}</span>
                        <span>Price: {request.price}</span> 
                        <span>Seller: {props.seller}</span>
                        <br></br>
                        <span>Description</span>
                        <p className="text-justify">{request.description}</p>
                </div>
                 <div className="control-btns">
                    <button onClick={props.onBack}>Back</button>
                    <button onClick={() => props.onApprove(request.requestid)}>Approve</button>
                    <button onClick={() => props.onRemove(request.requestid)}>Remove</button>
                </div> 
            </div>   
        </div>  
    )
}


class AdminRequestsPage extends React.Component{

    constructor(props){
        super(props)

        this.state = {
            selectedRequest: null,
            requests:[],
            seller:"",
            branchOptions:[],
            branchValue:"default"
        }
    }


    static contextType = AuthContext

    componentDidMount(){

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/requests',{
            method:"GET",
            headers:{
                'Content-Type':'application/json',
                'Authorization':'bearer '+this.context.token
            }
        })
        .then(response => response.json())
        .then(resData => {
            console.log(resData)
            this.fetchBranches(resData)
            //this.setState({
              //  requests:resData
            //})    
        })
        .catch(err => console.log(err))
    }



    fetchBranches = (requests) => {

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/buybook/branches',{
            method:"GET",
            headers:{
                'Authorization': 'bearer '+this.context.token
            }
        })
        .then(response => response.json())
        .then(resData => {
            console.log(resData)
            const branchOptions = resData.map((branch) => {
                return{
                    branchid :branch.branchid,
                    branchname:branch.branchname
                }
            })
            this.setState({
                branchOptions:branchOptions,
                requests:requests
            })
        })
    }



    handleViewClick = (requestid, seller) => {
            //APi
        console.log(requestid,seller)
        this.setState({
            selectedRequest:requestid,
            seller:seller
        })
    }

    handleBackClick = () => {
        this.setState({
            selectedRequest :null,
            seller:""
        })
    }





    handleRemoveClick = (requestid) => {

        console.log(requestid)

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/requests/decline/'+requestid, {
            method:"DELETE",
            headers:{
                'Content-Type':'application/json',
                'Authorization':'bearer '+this.context.token
            }
        }).then(response => response.json())
        .then(resData => {
            const updatedRequests = this.state.requests.filter((request)=>{
                return request.requestid !== requestid
            })
            this.setState({
                requests:updatedRequests,
                 selectedRequest :null,
                 seller:""
            })
        }).catch(err => {
            console.log(err)
        })
    }




    handleApproveClick = (requestid) => {

        if(this.state.branchValue === "default")
            return alert('Select Any Branch')

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/requests/approve', {
            method:"POST",
            headers:{
                'Content-Type':'application/json',
                'Authorization':'bearer '+this.context.token
            },
            body:JSON.stringify({
                requestid:requestid,
                branchid:this.state.branchValue
            })
        }).then(response => response.json())
        .then(resData => {

            const updatedRequests = this.state.requests.filter((request)=>{
                return request.requestid !== requestid
            })
            this.setState({
                requests:updatedRequests,
                 selectedRequest :null,
                 seller:"",
                 branchValue:"default"
            })
        }).catch(err => {
            console.log(err)
        })
    }


    handleBranchChange = (event) => {
        console.log(event.target.value)
        this.setState({
            branchValue: event.target.value
        })
    }




    render(){
        return(
           this.state.selectedRequest ? 
                <SelectedRequest seller={this.state.seller}
                                 context={this.context}
                                 requestId={this.state.selectedRequest}
                                 onBack={this.handleBackClick}
                                 onApprove={this.handleApproveClick}
                                 onRemove={this.handleRemoveClick}
                                 onChange={this.handleBranchChange}
                                 options={this.state.branchOptions}
                                 value={this.state.branchValue}/> : 
                <ViewRequests onView={this.handleViewClick} requests={this.state.requests}/>
        )        
    }
}





export default AdminRequestsPage