import React from 'react'
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import './App.css'
import AuthContext from './context/auth-context'

import NavBar from './components/NavBar/NavBar'
import HomePage from './pages/HomePage'
import AboutPage from './pages/AboutPage'
import AuthPage from './pages/AuthPage'
import BuyBookPage from './pages/BuyBookPage'
import MyCartPage from './pages/MyCartPage'
import SellPage from './pages/SellPage'
import AdminRequestsPage from './pages/AdminRequestsPage'
import AdminProductsPage from './pages/AdminProductsPage'

import Books from './components/BuyBook/Books/Books'

class App extends React.Component {
  // State for managing authentication
  state = {
    token: null,
    userId: null,
    firstname: '',
    signedIn: false,
    isAdmin: false,
  }

  componentDidMount() {
    const token = localStorage.getItem('token')
    const expiryDate = localStorage.getItem('expiryDate')
    const userId = localStorage.getItem('userId')
    const fname = localStorage.getItem('fname')
    const isAdmin = localStorage.getItem('isAdmin')
    //const lname = localStorage.getItem('lname')

    // If token doesn't exists
    if (!token || !expiryDate || !userId) {
      return
    }

    // Check for expiration time
    if (new Date(expiryDate) <= new Date()) {
      this.logout()
      return
    }

    const remainingMilliseconds =
      new Date(expiryDate).getTime() - new Date().getTime()
    this.setState({
      userId: userId,
      token: token,
      firstname: fname,
      signedIn: true,
      isAdmin: isAdmin === "true" ? true : false,
    })
    this.setAutoLogout(remainingMilliseconds)
  }

  setAutoLogout = (milliseconds) => {
    setTimeout(() => {
      this.logout()
    }, milliseconds)
  }

  // On successful Login get the token value recieved from server and store it in state
  login = (token, userId, tokenExpiration, firstname, isAdmin) => {
    this.setState({
      token: token,
      userId: userId,
      firstname: firstname,
      signedIn: true,
      isAdmin,
    })

    localStorage.setItem('token', token)
    localStorage.setItem('userId', userId)
    localStorage.setItem('fname', firstname)
    localStorage.setItem('isAdmin', isAdmin)
    const remainingMilliseconds = tokenExpiration * 60 * 60 * 1000
    console.log(remainingMilliseconds)
    const expiryDate = new Date(new Date().getTime() + remainingMilliseconds)
    console.log(expiryDate)
    localStorage.setItem('expiryDate', expiryDate.toLocaleString())
    this.setAutoLogout(remainingMilliseconds)
  }

  // On logging out reset the auth state
  logout = () => {
    this.setState({
      token: null,
      userId: null,
      firstname: '',
      signedIn: false,
      isAdmin: false,
    })

    localStorage.removeItem('token')
    localStorage.removeItem('userId')
    localStorage.removeItem('expiryDate')
    localStorage.removeItem('fname')
    localStorage.removeItem('isAdmin')
  }

  render() {

    console.log('IS ADMIN')
    console.log(this.state.token)

    return (
      <React.Fragment>
        <BrowserRouter>
          <AuthContext.Provider
            value={{
              token: this.state.token,
              userId: this.state.userId,
              firstname: this.state.firstname,
              login: this.login,
              logout: this.logout,
            }}
          >
            <NavBar
              logout={this.logout}
              isAdmin={this.state.isAdmin}
              isSignedIn={this.state.signedIn}
            />
            <Switch>
              {/* <Books /> */}
              {!this.state.token && <Redirect from='/buy' to='/auth' />}
              {!this.state.token && <Redirect from='/sell' to='/auth' />}
              {!this.state.token && <Redirect from='/cart' to='/auth' />}
              {!this.state.token && (
                <Redirect from='/admin/products' to='/auth' />
              )}
              {!this.state.token && (
                <Redirect from='/admin/requests' to='/auth' />
              )}
              {this.state.token && <Redirect from='/auth' to='/' />}
              {!this.state.token && (
                <Route path='/auth'>
                  <AuthPage />
                </Route>
              )}
              <Route path='/about'>
                <AboutPage />
              </Route>
              {this.state.token && !this.state.isAdmin && (
                <Route path='/buy'>
                  <BuyBookPage />
                </Route>
              )}
              {this.state.token && !this.state.isAdmin && (
                <Route path='/sell'>
                  <SellPage
                    btnText='Sell Book'
                    url={process.env.REACT_APP_BASE_URL + 'bookstore/sellbook'}
                  />
                </Route>
              )}
              {this.state.token && !this.state.isAdmin && (
                <Route path='/cart'>
                  <MyCartPage />
                </Route>
              )}
              {this.state.token && this.state.isAdmin && (
                <Route path='/admin/products'>
                  <AdminProductsPage />
                </Route>
              )}
              {this.state.token && this.state.isAdmin && (
                <Route path='/admin/requests'>
                  <AdminRequestsPage />
                </Route>
              )}
              <Route exact path='/'>
                <HomePage />
              </Route>
            </Switch>
          </AuthContext.Provider>
        </BrowserRouter>
      </React.Fragment>
    )
  }
}

export default App
