import React from 'react'
import SelectedBook from '../SelectedBook/SelectedBook'
import ChatBox from '../ChatBox/ChatBox'
import AuthContext from '../../../context/auth-context'
import './Books.css'

class Books extends React.Component {
  constructor(props) {
    super(props)

    console.log('Books', props)

    this.state = {
      booklist: [],
      selectedBook: null,
      showChat: false,
    }
  }

  static contextType = AuthContext

  componentDidMount() {
    //API call
    const branchId = this.props.bookCategory.branchId
    // const authHeader =
    //   'bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImRAbSIsInVzZXJJZCI6InVzZXItbml0dHQ2Zm5rZjFmN2VxZSIsImlhdCI6MTYxMTQzMTM3MywiZXhwIjoxNjExNDM0OTczfQ.7In-dhYnNXHW0TIEtA2wMWCKrLl8rk8RTeSIGf-Pod8'

    console.log(this.context.token)
    const authHeader = 'bearer ' + this.context.token

    fetch(
      process.env.REACT_APP_BASE_URL + 'bookstore/buybook/books/' + branchId,
      {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: authHeader,
        },
      }
    )
      .then((response) => response.json())
      .then((resData) => {
        console.log('--BuyBook : Books ', resData)
        this.setState({
          booklist: resData,
        })
      })
  }

  handleBookSelection = (bookid) => {
    console.log(bookid)
    const selectedBook = this.state.booklist.filter((book) => {
      return book.bookid === bookid
    })
    this.setState({
      selectedBook: selectedBook[0],
    })
  }

  handleBack = () => {
    this.setState({
      selectedBook: null,
      showChat: false,
      sellerid: null,
    })
  }

  handleContact = (sId) => {
    if (sId) {
      return this.setState({
        showChat: true,
        sellerid: sId,
      })
    }
    this.setState({
      showChat: false,
      sellerid: null,
    })
  }

  handleAddToCart = (isAdded, bookid) => {
    //API to add to cart
    console.log(bookid, isAdded)

    const requestBody = {
      userid: this.context.userId,
      bookid: bookid,
    }

    console.log(requestBody)

    const method = isAdded ? 'POST' : 'DELETE'

    console.log(method)

    const authHeader = 'bearer ' + this.context.token
    fetch(process.env.REACT_APP_BASE_URL + 'bookstore/cart/', {
      method: method,
      headers: {
        'Content-Type': 'application/json',
        Authorization: authHeader,
      },
      body: JSON.stringify(requestBody),
    })
      .then((response) => response.json())
      .then((resData) => {
        console.log('--Cart : ', resData)
      })
  }

  render() {
    const booklist = this.state.booklist.map((book) => {
      const bookimg = process.env.REACT_APP_BASE_URL + 'images/' + book.image

      return (
        <div className='book-container' key={book.bookid}>
          <button
            className='book-btn'
            onClick={this.handleBookSelection.bind(this, book.bookid)}
          >
            <img className='book-img' src={bookimg} alt='book' />
          </button>
          <p>{book.title}</p>
        </div>
      )
    })

    return !this.state.selectedBook ? (
      <React.Fragment>
        <div
          className='container d-flex w-50'
          style={{ marginBottom: '10px', marginTop: '20px' }}
        >
          <p className='p-2 w-100 bd-highlight' style={{ margin: 0 }}>
            {this.props.bookCategory.branchName}
          </p>
          <button
            className='p-2 flex-shrink-1 bd-highlight btn btn-primary'
            onClick={this.props.onBack}
          >
            Back
          </button>
        </div>
        <div className='container d-flex flex-wrap justify-content-between'>
          {booklist}
        </div>
      </React.Fragment>
    ) : (
      <div className='row m-auto'>
        <div className='col'>
          <SelectedBook
            book={this.state.selectedBook}
            onBack={this.handleBack}
            onCart={this.handleAddToCart}
            onContact={this.handleContact}
            context={this.context}
          />
        </div>
        {this.state.showChat ? (
          <div className='col'>
            <ChatBox
              bookid={this.state.selectedBook.bookid}
              userid={this.context.userId}
              sellerid={this.state.sellerid}
            />
          </div>
        ) : null}
      </div>
    )
  }
}

export default Books

