import React from 'react'
import Branches from '../components/BuyBook/Branches/Branches'
import Books from '../components/BuyBook/Books/Books'
import AuthContext  from '../context/auth-context'


class BuyBookPage extends React.Component{

    constructor(props){
        super(props)

        this.branches = []

        this.state = {
            isBranchSelected : false,
            bookCategory: null,
            branches : []
        }

    }

    static contextType = AuthContext


    componentDidMount(){

        const authHeader = "bearer "+this.context.token
        fetch(process.env.REACT_APP_BASE_URL+'bookstore/buybook/branches',{
            method:"GET",
            headers:{
                'Content-Type':'application/json',
                'Authorization': authHeader
            }
        })
        .then( response => response.json())
        .then( resData => {
            console.log('--BuyBook : Branches ',resData)
            this.setState({
                branches : resData
            })
        })
    }



    handleBranchClick = (event, id, bookCategoryName) => {
        console.log(id)
        this.setState({
            isBranchSelected: true,
            bookCategory: {
                branchName:bookCategoryName,
                branchId:id
            }
        })
    }


    handleBackClick = () => {

        this.setState({
             isBranchSelected : false,
             bookCategory: null
        })


    }

    render(){
         return (
            <React.Fragment>
                {!this.state.isBranchSelected ? <Branches onClick={this.handleBranchClick} branches={this.state.branches}/> : null}
                {this.state.isBranchSelected ? <Books onBack={this.handleBackClick} bookCategory={this.state.bookCategory} branch/>: null} 
            </React.Fragment>
          )
    }
}

export default BuyBookPage


