import React, { useContext, useEffect, useState } from 'react'
import io from 'socket.io-client'
import ScrollToBottom from 'react-scroll-to-bottom'

import AuthContext from '../../../context/auth-context'

import './ChatBox.css'

const ChatBoxList = ({ bookid, sellerid, onClick }) => {
  const context = useContext(AuthContext)
  const [chatUsers, setChatUsers] = useState([])

  useEffect(() => {
    const authHeader = 'bearer ' + context.token
    fetch(process.env.REACT_APP_BASE_URL + 'bookstore/chat/users', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: authHeader,
      },
      body: JSON.stringify({
        bookid,
        sellerid,
      }),
    })
      .then((response) => response.json())
      .then((resData) => {
        console.log(resData)
        setChatUsers(resData)
      })
  }, [])

  const chatUserList = chatUsers.map((cu) => {
    console.log(cu)
    return (
      <ChatBoxItem onClick={onClick} key={cu.userid} chatUser={{ ...cu }} />
    )
  })

  return <ul className='list-group list-group-flush'>{chatUserList}</ul>
}

const ChatBoxItem = ({ chatUser: { userid, name }, onClick }) => {
  console.log(name)
  return (
    <li
      className='list-group-item'
      style={{ cursor: 'pointer' }}
      onClick={() => onClick(userid)}
    >
      <h6 style={{ pointerEvents: 'none' }} className='mb-0'>
        {name}
      </h6>
      <p style={{ lineHeight: 1 }} className='mb-0'>
        <small></small>
      </p>
    </li>
  )
}

// --------------------------- //

const ChatItem = ({ own, message, time }) => {
  const style = {
    marginLeft: 'auto',
    backgroundColor: '#00a9de',
    color: '#fff',
  }
  return (
    <div className='chat-message' style={own ? style : {}}>
      <div className='date'>{new Date(time).toLocaleString()}</div>
      <div style={{ maxWidth: '500px' }}>{message}</div>
    </div>
  )
}

let socket
const ChatWindow = ({ bookid, sellerid, userid, setHeading }) => {
  const context = useContext(AuthContext)
  const inputRef = React.createRef('')

  const [messages, setMessages] = useState([])
  console.log(messages)

  useEffect(() => {
    const authHeader = 'bearer ' + context.token
    fetch(process.env.REACT_APP_BASE_URL + 'bookstore/chat/messages', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: authHeader,
      },
      body: JSON.stringify({
        bookid,
        sellerid,
        userid,
      }),
    })
      .then((response) => response.json())
      .then((resData) => {
        console.log(resData)
        setMessages(resData.chats)
        setHeading(resData.name)
      })
  }, [])

  useEffect(() => {

    let fromto = context.userId
    fromto = fromto + ((context.userId === sellerid ) ? userid : sellerid)
    console.log(sellerid)
    console.log(fromto)
    console.log(userid)
    console.log(context.userId)

    socket = io(process.env.REACT_APP_BASE_URL)
    socket.emit('hello', { fromto }, (s) => {
      console.log(s)
    })
    return () => {
      socket.disconnect()
      socket.off()
    }
  }, [context.userId])

  useEffect(() => {
    socket.on('recieveMessage', ({ chat }) => {
      console.log('recieveMessage: ')
      console.log(chat)
      setMessages([...messages, chat])
    })
  }, [messages])

  // display list of messages
  const chatItems = messages.map(({ sentby, message, timestamp }) => {
    return (
      <ChatItem
        key={timestamp}
        message={message}
        own={sentby === context.userId ? true : false}
        time={timestamp}
      />
    )
  })

  const handleSend = () => {
    console.log(inputRef.current.value)
    const m = inputRef.current.value
    const sentby = context.userId
    inputRef.current.value = ''
    socket.emit(
      'sendMessage',
      { bookid, sellerid, userid, message: m, sentby },
      ({ chat, error }) => {
        console.log(chat)
        if (chat) {
          setMessages([...messages, chat])
        }
      }
    )
  }

  return (
    <div>
      <ScrollToBottom debug={false} className='chat-box'>{chatItems}</ScrollToBottom>
      {/* <ul
        className='list-group scroll'
        style={{
          height: '500px',
          overflow: 'hidden',
          overflowY: 'scroll',
          padding: '1rem',
        }}
      >
        {chatItems}
      </ul> */}
      <div
        className='input-group m-auto'
        style={{ position: 'sticky', bottom: 0 }}
      >
        <input
          ref={inputRef}
          type='text'
          className='form-control'
          placeholder='Type a message..'
        />
        <div className='input-group-append'>
          <button
            onClick={handleSend}
            className='btn btn-outline-primary'
            type='button'
          >
            Send
          </button>
        </div>
      </div>
    </div>
  )
}

const ChatBox = ({ userid, sellerid, bookid }) => {
  const [toUser, setToUser] = useState(userid)
  const [heading, setHeading] = useState('Messages')

  const childComponent =
    toUser !== sellerid ? (
      <ChatWindow
        bookid={bookid}
        userid={toUser}
        sellerid={sellerid}
        setHeading={setHeading}
      />
    ) : (
      <ChatBoxList
        onClick={(id) => setToUser(id)}
        bookid={bookid}
        sellerid={sellerid}
      />
    )

  return (
    <div className='card mt-2 '>
      <div className='card-header'>
        {heading}{' '}
        {userid === sellerid && heading !== 'Messages' && (
          <span>
            <button
              onClick={() => {
                setToUser(sellerid)
                setHeading('Messages')
              }}
            >
              X
            </button>
          </span>
        )}
      </div>
      {childComponent}
    </div>
  )
}

export default ChatBox
