import React from 'react'

import './Branches.css'
import BranchBox from './BranchBox/BranchBox'


export default function Branches(props){

    console.log(props.branches)

    const branches = props.branches.map((branch,i) => {

        return(
         <BranchBox
                key={branch.branchid}
                id={branch.branchid}
                classes={branch.classes}
                branchName={branch.branchname}
                branchDetails={branch.branchdetails}
                colour={branch.colour}
                onClick={props.onClick}
            />)
    })

    return(
        <div className="dabba">
            {branches}
        </div> 
    )
}