import React from 'react'

function OptionSelector(props){
    return(
             <React.Fragment>
               <div  className="container w-50 d-flex">
                    <select value={props.value} onChange={props.onChange} className="form-control form-control-lg">
                        <option value="default">{props.defaultOption}</option>
                        {props.options}
                    </select>
                {props.btnText && <button  onClick={props.onClick} className="btn btn-primary" style={{width:100}}>{props.btnText}</button>}
               </div>
                {/*<div style={{display:"block", border:"0.5px solid black",width:"90"}}></div>*/}
            </React.Fragment>
    )
}

export default OptionSelector