import React from 'react'

import './SignIn.css'

class SignIn extends React.Component{

    // eslint-disable-next-line no-useless-constructor
    constructor(props){
        super(props)
        this.state = {
            email: "",
            password: "",
            isAdmin:false
        }
    }


    handleInput = (event) => {
        
        if(event.target.name === "isAdmin"){
            return this.setState({
                isAdmin: !this.state.isAdmin
            })
        }
        this.setState({
            [event.target.name] : event.target.value
        })
    }

    handleLogin = async() => {

        console.log(this.state)
        console.log(this.props)
        let url = process.env.REACT_APP_BASE_URL+'bookstore/auth/login/'

        if(this.state.isAdmin)
            url =process.env.REACT_APP_BASE_URL+'bookstore/admin/login/'

      try{
         const response = await fetch(url,{
            method : 'POST',
            headers: {
                'Content-Type':'application/json'
            },
            body: JSON.stringify({
                email:this.state.email,
                password:this.state.password
            })
        })

        const resData = await response.json()
        
        console.log(resData)
        this.props.login(resData.token, resData.userid,1,resData.firstname,this.state.isAdmin)
      }catch(err){
          console.log(err)
      }
    }

    render(){

        return(

            <div className="container h-100">
		    <div className="d-flex justify-content-center h-100">
			<div className="user_card">
				<div className="d-flex justify-content-center">
					<div className="brand_logo_container">
						<img src="/images/logo.jpg" className="brand_logo" alt="Logo"/>
					</div>
				</div>
				<div className="d-flex justify-content-center form_container">
					<form>
						<div className="input-group mb-3">
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-user"></i></span>
							</div>
							<input type="text" 
                                   name="email" 
                                   onChange={this.handleInput} 
                                   className="form-control input_user" 
                                   value={this.state.email} 
                                   placeholder="Email"/>
						</div>
						<div className="input-group mb-2">
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-key"></i></span>
							</div>
							<input type="password" 
                                   name="password" 
                                   onChange={this.handleInput} 
                                   className="form-control input_pass" 
                                   value={this.state.password} 
                                   placeholder="Password"/>
						</div>
                        <div className="form-check">
                            <input type="checkbox"
                                    name="isAdmin" 
                                    checked={this.state.isAdmin}
                                    onChange={this.handleInput} 
                                    className="form-check-input"/>
                            <label className="form-check-label">Admin</label>
                        </div>        
					</form>
				</div>


				<div className="d-flex justify-content-center mt-3 login_container">
					<button type="button" name="button" className="btn login_btn" onClick={this.handleLogin}>Login</button>
				</div>
				<div className="mt-4">
					<div className="d-flex justify-content-center links">
						Don't have an account? <button className="ml-2" onClick={this.props.handleSwitch}>Sign Up</button>
					</div>
				</div>
			</div>
		</div>
	</div>
        )
    }
}


export default SignIn