import React from 'react'
import CreateBranch from '../components/AdminProducts/CreateBranch'
import OptionSelector from '../components/AdminProducts/OptionSelector'
import AddBook from './SellPage'
import AuthContext from '../context/auth-context'


function AdminNavBar(props){
     return (
    <React.Fragment>
        <nav className="navbar navbar-light bg-dark">
                <button 
                    name="branch"
                    className="btn btn-outline-success" 
                    style={{marginBottom:"4px"}} 
                    type="button"
                    onClick={props.switch}>Manage Branches</button>
                <button 
                    name="books"
                    className="btn btn-outline-success" 
                    style={{marginBottom:"4px"}} 
                    type="button"
                    onClick={props.switch}>Manage Books</button>
        </nav>
    </React.Fragment>
    )
}







class AdminProductsPage extends React.Component{


    constructor(props){
        super(props)

        this.state = {
            isBranchManaged : true,
            branchValue:"default",
            bookValue:"default",
            bookOptions:[],
            branchOptions:[],
            isBookEnabled:false
        }
    }

    static contextType = AuthContext

    componentDidMount(){
        this.fetchBranches()
    }


    fetchBranches = () => {

        fetch(process.env.REACT_APP_BASE_URL+'bookstore/buybook/branches',{
            method:"GET",
            headers:{
                'Authorization': 'bearer '+this.context.token
            }
        })
        .then(response => response.json())
        .then(resData => {
            console.log(resData)
            const branchOptions = resData.map((branch) => {
                return{
                    branchid :branch.branchid,
                    branchname:branch.branchname
                }
            })
            this.setState({
                branchOptions:branchOptions
            })
        })

    }





    fetchBooks = async(branchid) => {

        if(!branchid)
            branchid = this.state.branchValue

        try{
             const response = await fetch(process.env.REACT_APP_BASE_URL+'bookstore/buybook/books/'+branchid,{
                 method:"GET",
                 headers:{
                     'Authorization':'bearer '+this.context.token
                 }
             })
            const resData = await response.json()
            console.log(resData)
            this.setState({
                branchValue:branchid,
                bookOptions:resData,
                isBookEnabled:true
            })
        }catch(err){
            console.log(err)
        }
    }


    handleBranchSelectOptions =(event) => {

        console.log(event.target.value)
        if(event.target.value === "default"){
            return this.setState({
                branchValue:event.target.value,
                isBookEnabled:false,
                bookValue:"default",
                bookOptions:[]
            })
        }

        if(!this.state.isBranchManaged)
            return this.fetchBooks(event.target.value)

        this.setState({
            branchValue:event.target.value
        })

    }

    handleBookSelectOptions = (event) => {
        console.log(event.target.value)
        this.setState({
            bookValue:event.target.value
        })

    }

    handleSwitchBranchBook = (event) => {
        
        const name = event.target.name
        if(name === "books" && this.state.isBranchManaged){
            return this.setState({
                isBranchManaged:false,
                branchValue:"default",
                bookValue:"default"
            })
        }
        if(name === "branch" && !this.state.isBranchManaged){
            return this.setState({
                isBranchManaged:true,
                branchValue:"default",
                bookValue:"default"
            })
        }
    }


    handleBookDelete = () => {

        if(this.state.bookValue === "default")
            return alert('Select Any Book')

        const bookid = this.state.bookValue
        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/book/'+bookid,{
            method:"DELETE",
              headers:{
                     'Authorization':'bearer '+this.context.token
                }
        }).then(response => response.json())
        .then(resData => {

            const updatedBooks = this.state.bookOptions.filter((book) => {
                return book.bookid !== bookid
            })

            this.setState({
                bookOptions : updatedBooks,
                bookValue:"default"
            })
        })
        .catch(err => {
            console.log(err)
        })
    }


    handleBranchDelete = () => {

        if(this.state.branchValue === "default")
            return alert('Select Any Branch')

        const branchid = this.state.branchValue
        fetch(process.env.REACT_APP_BASE_URL+'bookstore/admin/branch/'+branchid,{
            method:"DELETE",
              headers:{
                     'Authorization':'bearer '+this.context.token
                }
        }).then(response => response.json())
        .then(resData => {

            const updatedBranches = this.state.branchOptions.filter((branch) => {
                return branch.branchid !== branchid 
            })

            this.setState({
                branchOptions:updatedBranches,
                branchValue:"default"
            })
        })
        .catch(err => {
            console.log(err)
        })

    }


    render(){


        console.log(this.state.branchValue)
        const branchOptions = this.state.branchOptions.map((branch)=>{
            return <option key={branch.branchid} value={branch.branchid}>{branch.branchname}</option>
        })

        let bookOptions = []
        if(this.state.isBookEnabled){
            bookOptions = this.state.bookOptions.map((book) => {
                return <option key={book.bookid} value={book.bookid}>{book.title}</option>
            })
        }


        return (
            <React.Fragment>
                <AdminNavBar switch={this.handleSwitchBranchBook}/>
                <div style={{marginTop:"1rem", marginBottom:"1rem"}} className="container w-100 d-flex">
                    <OptionSelector 
                            btnText={this.state.isBranchManaged ? "Delete": null}
                            defaultOption="Select Any Branch"
                            value={this.state.branchValue}
                            onChange={this.handleBranchSelectOptions}
                            options={branchOptions}
                            onClick={this.handleBranchDelete}
                            />
                    {!this.state.isBranchManaged && 
                      this.state.isBookEnabled && 
                      <OptionSelector btnText="Delete" 
                                      options={bookOptions}
                                      defaultOption="Select Any Book"
                                      value={this.state.bookValue}
                                      onChange={this.handleBookSelectOptions}
                                      onClick={this.handleBookDelete}
                                      />} 
                
                </div>
                {this.state.isBranchManaged && <CreateBranch reflect={this.fetchBranches}/>}
                {!this.state.isBranchManaged && (this.state.branchValue!=="default") && <AddBook reflect={this.fetchBooks} btnText="Add Book" url={process.env.REACT_APP_BASE_URL+'bookstore/admin/book'} branchid={this.state.branchValue}/>  } 
            </React.Fragment>
        )
    }
}




export default AdminProductsPage