import React from 'react'

import './SignUp.css'

class SignUp extends React.Component{

    // eslint-disable-next-line no-useless-constructor
    constructor(props){
        super(props)

        this.state = {
            "firstname" :"",
            "lastname": "",
            "email": "",
            "password": ""
        }
    }



    handleInput = (event) => {
        this.setState({
            [event.target.name] : event.target.value
        })
    }

     handleSignUp = async() => {
        
        const response = await fetch(process.env.REACT_APP_BASE_URL+'bookstore/auth/signup',{
            method : 'POST',
            headers: {
                'Content-Type':'application/json'
            },
            body: JSON.stringify({
                ...this.state
            })
        })

        const resData = await response.json()
        console.log(resData)   
        this.setState({
            "firstname" :"",
            "lastname": "",
            "email": "",
            "password": ""
        })

        this.props.handleSwitch()

    }

    render(){

        return(

            <div className="container h-100">
		    <div className="d-flex justify-content-center  h-100">
			<div className="user_card">
				<div className="d-flex justify-content-center">
					<div className="brand_logo_container">
						<img src="/images/logo.jpg" className="brand_logo" alt="Logo"/>
					</div>
				</div>
				<div className="d-flex justify-content-center form_container" >
					<form>
                        <div className="input-group mb-3" style={{marginTop: 10}}>
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-user"></i></span>
							</div>
							<input 
                                type="text" 
                                name="firstname" 
                                className="form-control input_user" 
                                value={this.state.firstname} 
                                onChange={this.handleInput}
                                placeholder="Firstname"/>
						</div>
                        <div className="input-group mb-3">
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-user"></i></span>
							</div>
							<input 
                                type="text" 
                                name="lastname" 
                                className="form-control input_user" 
                                value={this.state.lastname} 
                                onChange={this.handleInput}
                                placeholder="Lastname"/>
						</div>


						<div className="input-group mb-3">
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-user"></i></span>
							</div>
							<input 
                                type="text" 
                                name="email" 
                                className="form-control input_user" 
                                value={this.state.email} 
                                onChange={this.handleInput}
                                placeholder="Email"/>
						</div>

						<div className="input-group mb-2">
							<div className="input-group-append">
								<span className="input-group-text"><i className="fas fa-key"></i></span>
							</div>
							<input type="password"
                                   name="password" 
                                   className="form-control input_pass" 
                                   onChange={this.handleInput}
                                   value={this.state.password} 
                                   placeholder="Password"/>
						</div>
					</form>
				</div>
				<div className="d-flex justify-content-center mt-3 login_container">
					<button type="button" name="button" className="btn login_btn" onClick={this.handleSignUp}>Sign Up</button>
				</div>
				<div className="mt-4">
					<div className="d-flex justify-content-center links">
						Already have an account? <button className="ml-2" onClick={this.props.handleSwitch}>Log In</button>
					</div>
				</div>
			</div>
		</div>
	</div>
        )
    }
}


export default SignUp