import React from 'react'
import { NavLink } from 'react-router-dom'

import './NavBar.css'


function NavBar(props){

    console.log('NAV PROPS')
    console.log(props)


    const signIn =  props.isSignedIn ? <button id="btn-logout" onClick={props.logout}>Logout</button>    
                :<li className="nav-item">
                         <NavLink exact to="/auth" className="nav-link">Log in</NavLink>
                </li>


    return (
        <section id="nav-bar">
            <nav className="navbar navbar-expand-lg navbar-light bg-dark">
                <a className="navbar-brand" href="/"><img src="/images/logo.jpg" alt="logo"/></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ml-auto">
                    <li className="nav-item">
                       <NavLink exact to="/" className="nav-link">Home</NavLink>
                    </li>
                    <li className="nav-item">
                         <NavLink exact to="/about" className="nav-link">About Us</NavLink>
                    </li>
                    <li className="nav-item">
                        {props.isAdmin? 
                         <NavLink exact to="/admin/products" className="nav-link">Products</NavLink> :
                         <NavLink exact to="/buy" className="nav-link">Buy</NavLink>
                        }  
                    </li>
                    <li className="nav-item">
                        {props.isAdmin ? 
                         <NavLink exact to="/admin/requests" className="nav-link">Requests</NavLink> :
                         <NavLink exact to="/sell" className="nav-link">Sell</NavLink>
                        }  
                    </li>
                    {/* {!props.isAdmin && <li className="nav-item">
                         <NavLink exact to="/cart" className="nav-link">My Cart</NavLink>
                    </li>} */}
                    {signIn}
                    </ul>
                </div>
            </nav>
        </section>  

    )
}


export default NavBar