import React from 'react'

function OrderPlaced(){
    return(
        <div className="order-placed">
            <p>Order Placed</p>
        </div>
    )
}



class Checkout extends React.Component{

    state = {
        name: "",
        phone:"",
        address:"",
        orderPlaced:false
    }


    handleInput = (event) =>{ 
        this.setState({
            [event.target.name]:event.target.value
        })
    }

    handlePlaceOrder = () => {
        //API Save the order
        console.log(this.state)
        this.setState({
            orderPlaced: true
        })
    }

    render(){
        return (
            this.state.orderPlaced ? <OrderPlaced/> :  
            <div className="checkout-container">
                <div className="checkout-input">
                    <label>Name</label>
                    <input type="text" placeholder="Name" name="name"  onChange={this.handleInput} value={this.state.name}></input>
                </div>
                <div className="checkout-input">
                    <label>Phone</label>
                    <input type="text" placeholder="Phone Number" name="phone" onChange={this.handleInput} value={this.state.phone}></input>
                </div>
                <div className="checkout-input">
                    <label>Address</label>
                    <input type="text" placeholder="Address" name="address" onChange={this.handleInput} value={this.state.address}></input>
                </div>
                <button className="btn-primary btn" onClick={this.handlePlaceOrder}>Place Order</button>
            </div>
        )      
    }
}


export default Checkout
