import React from 'react'


export default function BranchBox(props){

    return(
         <div className ="box"
              style={{backgroundColor: props.colour}} 
              onClick={ (e) => { props.onClick(e,props.id, props.branchName)}}>
                <div className ="icon" style={{backgroundColor: props.colour}}>
                    <i className={"fa fa-"+props.classes} aria-hidden="true"></i>
                </div>
                <div className="content">     
                    <h3>{props.branchName}</h3>
                    <p>{props.branchDetails}</p>
                </div>
        </div>
    )    
}